.navbar-container{
    display: flex;
    justify-content: space-between;
    align-items: center;
    font-size: 1.2rem;
    padding: 0 30px;
    background-color: #222;
    box-shadow: 0 5px 15px rgba(0, 0, 0, 0.25);
    width: 95%;
    height: 80px;
    border-radius: 13px;
    position: fixed;
    top: 20px;
    left: 50%;
    transform: translate(-50%);
}

.navbar-logo{
    color: #fff;
    justify-self: start;
    cursor: pointer;
}

.nav-menu{
    display: grid;
    grid-template-columns: repeat(6, auto);
    grid-gap: 10px;
    list-style: none;
    align-items: center;
    text-align: center;
    justify-content: end;
}

.nav-links{
    text-decoration: none;
    color: #fff;
    padding: 0.7rem 1rem;
    white-space: nowrap;
}

.nav-links i{
    padding-right: 10px;
}

.nav-links:hover{
    background: rgb(80, 80, 238);
    border-radius: 4px;
    transition: all 0.2s ease-in-out;
}

.fa-bars{
    color: #fff;
    
}

@media screen and (max-width: 700px) {
    .navbar-container{
        display: flex;
        justify-content: space-between;
        align-items: center;
        font-size: 0.7rem;
        padding: 0 30px;
        background-color: #222;
        box-shadow: 0 5px 15px rgba(0, 0, 0, 0.25);
        width: 95%;
        height: 80px;
        border-radius: 13px;
        position: fixed;
        top: 20px;
        left: 50%;
        transform: translate(-50%);
    }
    
    .navbar-logo{
        color: #fff;
        justify-self: start;
        cursor: pointer;
    }
    
    .nav-menu{
        display: grid;
        grid-template-columns: repeat(6, auto);
        grid-gap: 5px;
        list-style: none;
        align-items: center;
        text-align: center;
        justify-content: end;
    }
    
    .nav-links{
        text-decoration: none;
        color: #fff;
        padding: 0.7rem 1rem;
        white-space: nowrap;
    }
    
    .nav-links i{
        padding-right: 10px;
    }
    
    .nav-links:hover{
        background: rgb(80, 80, 238);
        border-radius: 4px;
        transition: all 0.2s ease-in-out;
    }
    
    .fa-bars{
        color: #fff;
        
    }
}